import axios from 'axios'
import Api from '../api/api'
import Store from '@/store'
import { Loading, Message } from 'element-ui'
// Api循环遍历输出不同的请求方法
let instance = axios.create({
    // 基础url，会在请求url中自动添加前置链接
    baseURL:process.env.VUE_APP_BASE_API,
    timeout:1000
})
let loadingInstance = null
const Http = {} // 包裹请求方法的容器
// 请求格式/参数的统一
for(let key in Api){
    let api = Api[key]

    //params 请求参数，get/delete (url)，put/patch/post (data)
    // isFormData 标识是否form-data请求
    // config 请求配置参数
    Http[key] = async function(
        params,
        isFormData = false,
        config = {}
    ){
        let url = api.url
        let method = api.method
        let newParams;
        let res = {};
        // 判断content-type是否是form-data
        if(params && isFormData){
            newParams = new FormData()
            for(let key in params){
                newParams.append(key,params[key])
            }
        }else{
            newParams = params
        }
        if(method === 'put' || method === 'patch' || method === 'post'){
            try{
                console.log('url==',url)
                res = await instance[method](url,newParams,config)
            }catch(err){res = err}
        }else if(method === 'get' || method === 'delete'){
            try{
                config.params = newParams
                res = await instance[method](url,config)
            }catch(err){res = err}
        }
        return res
    }
}
// 拦截器的添加
instance.interceptors.request.use(config => {
    // 发起请求前做些什么
    loadingInstance = Loading.service({text:'加载中...'})
    const token = Store.getters.token
    if (token) {
        config.headers.Authorization = token // 请求头部添加token
    }
    return config
},err => {
    // 请求失败
    loadingInstance.close()
    Message.error('请求错误，请稍后重试')
    return err
})
instance.interceptors.response.use(res => {
    // 响应成功后做什么
    loadingInstance.close()
    return res.data
},err => {
    // 响应失败
    loadingInstance.close()
    Message.error('响应错误，请稍后重试')
    return err
})
export default Http;
