import Vue from 'vue'
import VueRouter from 'vue-router'
import Hello from '../views/Hello/Hello.vue'
import Store from '@/store'
import Layout from '@/views/Layout'
import { Message } from 'element-ui'

Vue.use(VueRouter)

export const routes = [
  {
    path: '/',
    name: 'Hello',
    component: Hello
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login/Login.vue')
  },
  {
    path: '/form',
    name: 'Form',
    component: () => import('../views/Form/Form.vue')
  },
  {
    path: '/layout',
    name: 'Home',
    component: Layout,
    redirect: '/dashbord',
    children: [
      {
        path: '/',
        name: 'Dashbord',
        component: () => import('@/views/dashboard'),
        meta: { title: '首页', icon: 'el-icon-s-data' }
      }
    ]
  }
]
/*动态添加routers*/
export const asyncRoutes = [
  {
    path: '/permission',
    name: 'Permission',
    component: Layout,
    redirect: '/permission/page-use',
    meta: {
      title: '权限许可',
      icon: 'el-icon-lock'
    },
    children: [
      {
        path: 'page-user',
        name: 'PageUser',
        component: () => import('@/views/permission/page-user'),
        meta: { title: '用户页面', icon: 'el-icon-user' }
      },
      {
        path: 'page-admin',
        name: 'PageAdmin',
        component: () => import('@/views/permission/page-admin'),
        meta: {
          title: '管理员页面',
          icon: 'el-icon-user-solid'
        }
      },
      {
        path: 'roles',
        name: 'Roles',
        component: () => import('@/views/permission/roles'),
        meta: { title: '权限设置', icon: 'el-icon-s-tools' }
      }
    ]
  },
  {
    path: '/error',
    component: Layout,
    name: 'Error',
    redirect: '/error/404',
    children: [
      {
        path: '404',
        name: 'Page404',
        component: () => import('@/views/error-page/404'),
        meta: { title: '404', icon: 'el-icon-s-release' }
      }
    ]
  },
  {
    path: '*',
    name: '*404',
    redirect: '/404',
    hidden: true
  }
]

const router = new VueRouter({
  routes
})

// 导航守卫
router.beforeEach(async (to, from, next) => {
  if (to.path === '/login' || to.path === '/') {
    next()
  } else {
    if (Store.getters.token) {
      const hasRoles = Store.getters.roles.length > 0
      if (hasRoles) {
        next()
      } else {
        try {
          const res = await Store.dispatch('user/getInfo')
          const { roles } = res.data
          const addRoutes = await Store.dispatch(
              'permission/getAsyncRoutes',
              roles
          )
          addRoutes.forEach((item) => {
            router.addRoute(item)
          })
          next({ ...to, replace: true }) // hack方法，确保addRoute已经完成
        } catch (error) {
          Message.error(error)
        }
      }
    } else {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    }
  }
})

export default router
